.screen {
    padding: 35px 0 45px;
    background-color: #fff;

    @include respond-min(md) {
        padding: 45px 0 60px;
    }

    &__title {
        position: relative;
        color: $gray--dark;
        font-family: $f-medium;
        font-weight: 500;
        font-size: 25px;
        line-height: 1.3;
        margin-bottom: 35px;
        text-align: center;

        @include respond-min(xs) {
            margin-bottom: 50px;
        }

        @include respond-min(sm) {
            line-height: 1.5;
            font-size: 35px;
        }

        &.mb-20 {
            margin-bottom: 20px;
        }

        &--white {
            color: #fff;
        } //.screen__title--white
    } //.screen__title

    &__desc {
        margin-bottom: 40px;
        font-family: $f;
        font-size: 16px;
        line-height: 1.7;
        color: $color-text;

        @include respond-min(md) {
            margin-bottom: 60px;
            text-align: center;
            font-size: 20px;
        }

        @include respond-min(sm) {
            text-align: center;
        }

        p + p {
            margin-top: 10px;
        }

        &.mb-15 {
            margin-bottom: 15px;
            line-height: 1;

            @include respond-to(sm) {
                line-height: 1.4;
            }
        }
    } //.screen__description

    &__icon-slider {
        margin-bottom: 20px;
        position: relative;

        @include respond-min(md) {
            width: 350px;
            margin: 0;
            order: 1;
            margin-right: auto;
        }

        @include screen(768px, 1024px) {
            max-width: 70%;
            margin: auto auto 20px;
        }

        .bb-slider__slide + .bb-slider__slide {
            @include respond-min(md) {
                margin-top: 20px;
            }
        }
    } //.screen__icon-slider

    &__btn {
        display: block;
        width: 100%;
        font-size: 17px;

        @include respond-min(md) {
            width: 300px;
            margin: auto;
        }

        @include respond-min(xs) {
            width: 400px;
            margin: auto;
        }
    } //.screen__btn

    &--img {
        @include sectionOverlay;
        @include cover;

        & > * {
            z-index: 1;
            color: #fff;
        }

        .screen {

            &__title,
            &__desc {
                color: #fff;
            }
        }

        .btn-default {

            &:not(.form__btn) {
                box-shadow: 0 2px 5px 0 $gray--dark;
            }
        }
    } //.screen--img

    &--get-icons {

        .container {
            @include respond-min(md) {
                max-width: 900px;
            }
        }

        .screen__inner {
            margin-bottom: 20px;

            @include respond-min(md) {
                display: flex;
                align-items: center;
                margin-bottom: 60px;
            }
        }

        .list--square {
            margin-bottom: 50px;
            @include respond-min(md) {
                margin-right: 60px;
                margin-bottom: 0;
            }
        }
    } //.screen--get-icons

    &--adv {

        .text-block {

            &__wrap {
                @include respond-min(md) {
                    flex-direction: row;
                    align-items: baseline;
                }
            }
        }

        img {
            @include respond-min(md) {
                width: 80%;
                margin: auto;
            }
        }

        .screen__img {
            @include respond-min(md) {
                display: block;
                width: 100%;
            }

            img {
                width: 80%;
                margin: auto;
            }
        }
    } //.screen--advantages

    &--reg {
        padding: 15px 0;

        @include respond-min(md) {
            display: none;
        }
    } //.screen--reg

    &--reviews {

        .bb-slider__slide {
            @include respond-min(md) {
                padding: 0 30px;
            }
        }
    } //.screen--reviews

    &--why-we {

        .text-block {

            @include respond-min(md) {
                margin-bottom: 0;
            }

            &__item {

                &:nth-child(4) {
                    margin-left: auto;
                }

                &:nth-child(5) {
                    margin-right: auto;
                }

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    } //.screen--why-we

    &--course {

        .icon-list__icon {
            border: 1px solid #fff;

            @include respond-min(md) {
                border: 2px solid #fff;
            }
        }
    } //.screen--course

    &.bg-grey {
        background: $color-bg--lightest;
    } //.screen.bg-grey

    .form {
        width: 100%;

        @include respond-min(md) {
            color: $color-text--darkest;
            background-color: rgba(255, 255, 255, 0.75);
            border-radius: $border-radius;
            margin: 0;
            margin-left: auto;
        }

        @include respond-to(xs) {
            width: 100%;
            max-width: 100%;
            padding: 10px 10px 20px;
        }

        &__title {
            width: 260px;
            font-family: $f-bold;
            font-size: 27px;
            line-height: 1.2;
            color: $gray--dark;
        }

        .bb-checkbox__box {
            border-color: $color-border;
        }

        .bb-checkbox--radio .bb-checkbox__title {
            font-size: 14px;
        }

        .bb-checkbox:hover .bb-checkbox__box {
            border-color: $color-main;
        }

        .bb-title--devider::after,
        .bb-title--devider::before {
            border-top: 1px solid $gray--light;
        }
    } //.form

    .buttons-box {

        .btn {
            min-width: 200px;

            & + .btn {
                margin-left: 15px;
            }
            @include respond-to(xs) {
                width: 100%;

                & + .btn {
                    margin-top: 20px;
                    margin-left: 0;
                }
            }
        }
    } //.screen .buttons-box

    .slick-dots {
        bottom: -24px;
    } //.screen .slick-dots
} //.screen
